@tailwind base;
@tailwind components;
@tailwind utilities;

/* @import url('https://fonts.googleapis.com/css2?family=Henny+Penny'); */
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans');

@layer components {
  .card {
    @apply grow px-10 py-8 my-5 md:mx-5 drop-shadow-lg hover:scale-[1.02] transition duration-150;
    border-radius: 5% ;
  }

  .card-bordecyan {
    @apply bg-white border-2 border-cyan-600;
  }

  .card-filled {
    @apply bg-gradient-to-tr from-cyan-500 via-cyan-400 to-cyan-200;
  }

  .card .card-title {
    @apply text-2xl underline text-white text-center py-2 drop-shadow;
  }

  .progress {
    @apply grid grid-cols-3 my-4;
  }

  .progress-bar-title {
    @apply font-semibold col-span-3 text-white;
  }

  .progress-bar {
    @apply col-span-3 bg-white text-cyan-100 rounded-full h-1;
  }

  .progress-bar .bar {
    @apply bg-cyan-900 h-1 rounded-full;
  }

  .project-card {
    @apply flex flex-col w-full px-7 md:px-11 pt-7 pb-10 mx-0 my-5 md:m-5 bg-gradient-to-br from-white via-white to-neutral-50 rounded-lg shadow-md border border-neutral-100 text-cyan-500;
  }

  .project-card .card-title {
    /* @apply px-10 py-5 text-xl font-bold text-cyan-50 z-10 shadow bg-gradient-to-tr from-cyan-800 via-cyan-700 to-cyan-600; */
    @apply text-2xl font-bold;
  }

  ul {
    @apply pl-5;
  }

  .project-card .card-body {
    @apply pt-5;
  }

  .education-card {
    @apply grid grid-cols-4;
  }

  .education-card .education-duration {
    @apply flex flex-row text-right text-cyan-500 border-r-2 border-cyan-600 py-4 md:px-4;
  }

  .education-card .education-details {
    @apply p-4 col-span-3;
  }

  .education-card .education-duration .year {
    @apply grow;
  }

  .education-card .education-duration .dot {
    @apply rounded-full bg-cyan-700 ml-5 mt-1.5 -mr-[6.5px] md:-mr-[22px];
    min-width: 10px;
    min-height: 10px;
    max-width: 10px;
    max-height: 10px;
    /* margin-right: -22px; */
    animation: bg-scale-animation 0.8s linear infinite;
  }

  .education-card .education-details .education-name {
    @apply text-cyan-500 font-bold text-2xl drop-shadow;
  }

  .education-card .education-details .education-description {
    @apply text-cyan-500;
  }

  ::marker {
    @apply text-cyan-700;
    animation: scale-animation 0.8s linear infinite;
  }

  .bg-box-1 {
    @apply bg-cyan-200 rounded-full absolute top-96 md:top-64 blur-sm -z-10;
    height: 100px;
    width: 100px;    
    left: 10px;
    animation: bg-box-animation-horizontal 35s -5s infinite;    
  }

  .bg-box-2 {
    @apply bg-cyan-200 rounded-full absolute md:top-64 blur-md -z-10;
    height: 200px;
    width: 200px;    
    right: 10px;
    top: 200px;
    animation: bg-box-animation-vertical 20s -5s infinite;    
  }

  .bg-box-3 {
    @apply bg-cyan-200 rounded-full absolute blur-sm -z-10;
    height: 60px;
    width: 60px;    
    left: 10px;
    bottom: 30px;
    animation: bg-box-animation-horizontal 60s -10s infinite;    
  }

  html {
    @apply tracking-wide;
    font-family: 'Josefin Sans';
    /* background: black; */
  }
  
  #home-image-box {
    border-radius: 30% 70% 70% 30% / 75% 74% 26% 25%;
    animation: image-box-animation 15s linear infinite;
  }
  
  @keyframes image-box-animation {
    10% {
      border-radius: 30% 70% 70% 30% / 75% 74% 26% 25%;
    }
    50% {
      border-radius: 74% 26% 70% 30% / 47% 46% 54% 53%;
    }
    100% {
      border-radius: 30% 70% 70% 30% / 75% 74% 26% 25%;
    }
  }

  @keyframes bg-box-animation-horizontal {
    10% {
      left: 10px;
    }    
    50% {
      left: 50%;
    }
    100% {
      left: 10px;
    }
  }

  @keyframes bg-box-animation-vertical {
    10% {
      top: 200px;
    }    
    50% {
      top: 400px;
    }
    100% {
      top: 200px;
    }
  }

  @keyframes scale-animation {
    10% {
      @apply text-cyan-200;
    }
    50% {
      @apply text-cyan-700;
    }
    100% {
      @apply text-cyan-900;
    }
  }

  @keyframes bg-scale-animation {
    10% {
      @apply bg-cyan-200;
    }
    50% {
      @apply bg-cyan-700;
    }
    100% {
      @apply bg-cyan-900;
    }
  }

  .loader {
    display: inline-block;
    position: relative;
    width: 60px;
    height: 60px;
    margin-right: 15px;
  }
  .loader div {
    position: absolute;
    /* border: 4px solid black; */
    opacity: 1;
    border-radius: 50%;
    animation: loader 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  }
  .loader div:nth-child(2) {
    animation-delay: -0.5s;
  }
  @keyframes loader {
    0% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 0;
    }
    4.9% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 0;
    }
    5% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 0px;
      left: 0px;
      width: 72px;
      height: 72px;
      opacity: 0;
    }
  }
  
}